.benefit {
  height: 600px;
  width: 100%;
  display: flex;
  align-items: center;
  
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__left {
    width: 47%;
  }

  &__right {
    width: 47%;
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 50px;
    line-height: 50px;

    letter-spacing: 0.3px;
  }

  &__info {
    font-size: 36px;
    line-height: 150%;

    letter-spacing: 0.3px;

    color: rgba(239, 237, 232, 0.65);
    text-align: justify;
  }
}